






































var _self, loginRes, page = 1;
export default {
	data() {
		return {
			myFace : '',
			arts : [],
			loadMore : "点击加载更多",
			user : []
		};
	},
	onLoad() {
		_self = this;
		loginRes = this.checkLogin('../write/write', 2);
		if (!loginRes) return;
		this.myFace = loginRes[3];
	},
	onShow:function(){
		//加载我的文章
		this.arts = [];
		page = 1;
		this.getArtList();
		// 加载会员信息
		uni.request({
			url: this.apiServer + 'my&m=info',
			method: 'POST',
			header: {'content-type' : "application/x-www-form-urlencoded"},
			data: {
				uid : loginRes[0],
				random : loginRes[1]
			},
			success: res => {
				console.log(res);
				if(res.data.status == 'ok'){
					this.user = res.data.data;
				}
			}
		});
	},
	methods:{
		// 注销
		logoff : function(){
			uni.removeStorageSync('SUID');
			uni.removeStorageSync('SRAND');
			uni.showToast({
				title:"您已经退出悦读",
				icon: "none"
			});
			setTimeout(function(){
				uni.switchTab({
					url:'../index/index'
				});
			}, 1000);
		},
		editArt : function(e){
			var artId = e.currentTarget.dataset.artid;
			uni.navigateTo({
				url:"../editArt/editArt?artId="+artId
			});
		},
		removeArt : function(e){
			var artId = e.currentTarget.dataset.artid;
			var index = e.currentTarget.dataset.index;
			uni.showModal({
				title:"提示",
				content:"确定要删除吗?",
				success:function(e){
					if(e.confirm == true){
						uni.request({
							url: '',
							method: 'GET',
							url: _self.apiServer + 'my&m=removeArt',
							method: 'POST',
							header: {'content-type' : "application/x-www-form-urlencoded"},
							data: {
								uid : loginRes[0],
								random : loginRes[1],
								artId : artId
							},
							success: res => {
								console.log(res);
								if(res.data.status == 'ok'){
									uni.showToast({title: "已删除", icon:"none"});
									_self.arts.splice(index, 1);
								}else{
									uni.showToast({title: "删除失败", icon:"none"});
								}
							}
						});
					}
				}
			});
		},
		getArtList : function(){
			if(this.loadMore != '点击加载更多'){return ;}
			this.loadMore = '加载中...';
			uni.request({
				url: this.apiServer + 'my&m=arts&page='+page,
				method: 'POST',
				header: {'content-type' : "application/x-www-form-urlencoded"},
				data: {
					uid : loginRes[0],
					random : loginRes[1]
				},
				success: res => {
					console.log(res);
					if(res.data.status == 'ok'){
						this.arts = this.arts.concat(res.data.data);
						page++;
						this.loadMore = '点击加载更多';
					}else if(res.data.status == 'empty'){
						this.loadMore = '已经加载全部';
					}else{
						this.loadMore = '点击加载更多';
					}
				}
			});
		}
	}
}
